import React from 'react'
import Section from '../Section'
import ExperienceUnit from '../ExperienceUnit'

import angularLogo from '../../assets/images/skills/angularjs.svg'
import pythonLogo from '../../assets/images/skills/pylogo.png'
import d3Logo from '../../assets/images/skills/d3.png'
import djangoLogo from '../../assets/images/skills/django.png'
import railsLogo from '../../assets/images/skills/rails-logo.png'
import highchartsLogo from '../../assets/images/skills/highchart.svg'
import reactLogo from '../../assets/images/skills/react.png'
import emberLogo from '../../assets/images/skills/ember-js.png'
import mysqlLogo from '../../assets/images/skills/mysql.png'
import mongoLogo from '../../assets/images/skills/mongo.png'
import rubyLogo from '../../assets/images/skills/ruby.png'
import SkillUnit from '../SkillUnit';

class SkillSection extends React.Component {
  render() {
    return (
      <Section title="Skills">
        <div className="row">
        <SkillUnit
            logo={angularLogo}
            colour='#FFFFFF'
            link='https://angularjs.org/'
            title='AngularJS'
          />
          <SkillUnit
            logo={pythonLogo}
            colour='#FFFFFF'
            link='https://pyhton.org/'
            title='Pyhton'
          />
          <SkillUnit
            logo={djangoLogo}
            colour='#FFFFFF'
            link='https://www.djangoproject.com/'
            title='django'
          />
          <SkillUnit
            logo={railsLogo}
            colour='#FFFFFF'
            link='http://rubyonrails.org/'
            title='Ruby on Rails'
          />
          <SkillUnit
            logo={d3Logo}
            colour='#FFFFFF'
            link='https://d3js.org/'
            title='D3'
          />
          <SkillUnit
            logo={highchartsLogo}
            colour='#FFFFFF'
            link='https://www.highcharts.com/'
            title='Highcharts'
          />
          <SkillUnit
            logo={reactLogo}
            colour='#FFFFFF'
            link='https://reactjs.org/'
            title='ReactJS'
          />
          <SkillUnit
            logo={emberLogo}
            colour='#FFFFFF'
            link='https://www.emberjs.com/'
            title='EmberJS'
          />
          <SkillUnit
            logo={rubyLogo}
            colour='#FFFFFF'
            link='https://www.ruby-lang.org/en/'
            title='Ruby'
          />
          <SkillUnit
            logo={mysqlLogo}
            colour='#FFFFFF'
            link='https://www.mysql.com/'
            title='MysqlDB'
          />
          <SkillUnit
            logo={mongoLogo}
            colour='#FFFFFF'
            link='https://www.mongodb.com/'
            title='MongoDB'
          />
        </div>
      </Section>
    )
  }
}

export default SkillSection



